}

.site-footer blockquote {
  color: #555;
}

.site-footer .content {
  color: #c0c0c0;
  color: rgba(255, 255, 255, 0.65);
  font-size: 0.857em;
}

.site-footer .menu-item {
  padding: 0;
}

.site-footer .content a,
.site-footer .content a.is-active {
  color: #fcfcfc;
  color: rgba(255, 255, 255, 0.8);
}

.site-footer .content a:hover,
.site-footer .content a:focus {
  color: #fefefe;
  color: rgba(255, 255, 255, 0.95);
}

.site-footer .block {
  margin: 20px 0;
  border: 1px solid #444;
  border-color: rgba(255, 255, 255, 0.1);
}

.site-footer table {
  font-size: 1em;
}

.site-footer tr td,
.site-footer tr th {
  border-color: #555;
  border-color: rgba(255, 255, 255, 0.18);
}

.site-footer tr.odd {
  background-color: transparent;
}

.site-footer tr.even {
  background-color: #2c2c2c;
  background-color: rgba(0, 0, 0, 0.15);
}

/* Footer Top Styling. */
.site-footer__top h2 {
  border-bottom: 1px solid #555;
  border-color: rgba(255, 255, 255, 0.15);
  font-size: 1em;
  margin-bottom: 0;
  padding-bottom: 3px;
  text-transform: uppercase;
}

.site-footer__top .content {
  margin-top: 0;
}

.site-footer__top p {
  margin-top: 1em;
}

.site-footer__top .content .menu {
  padding-left: 0;
  /* LTR */
}

[dir="rtl"] .site-footer__top .content .menu {
  padding-right: 0;
}

.site-footer__top .content li a {
  display: block;
  border-bottom: 1px solid #555;
  border-color: rgba(255, 255, 255, 0.15);
  line-height: 1.2;
  padding: 0.8em 2px 0.8em 20px;
  /* LTR */
  text-indent: -15px;
}

[dir="rtl"] .site-footer__top .content li a {
  padding: 0.8em 20px 0.8em 2px;
}

.site-footer__top .content li a:hover,
.site-footer__top .content li a:focus {
  background-color: #1f1f21;
  background-color: rgba(255, 255, 255, 0.05);
  text-decoration: none;
}

.site-footer__top .block-menu,
.site-footer__bottom .block {
  margin: 0;
  padding: 0;
  border: none;
}

/* Footer Bottom Styling. */
.site-footer__bottom .block {
  margin: 0.5em 0;
}

.site-footer__bottom .content {
  padding: 0.5em 0;
  margin-top: 0;
}

.site-footer__bottom .block h2 {
  margin: 0;
}

.site-footer__bottom {
  letter-spacing: 0.2px;
  margin-top: 30px;
  border-top: 1px solid #555;
  border-color: rgba(255, 255, 255, 0.15);
}

.site-footer__bottom .region {
  margin-top: 20px;
}

.site-footer__bottom .block {
  clear: both;
}

/* We need to be specific to overwrite [dir="rtl"] .block ul in block.css. */
.site-footer__bottom .block .menu {
  padding: 0;
}

.site-footer__bottom .menu-item a {
  float: left;
  /* LTR */
  padding: 0 12px;
  display: block;
  border-right: 1px solid #555;
  /* LTR */
  border-color: rgba(255, 255, 255, 0.15);
}

[dir="rtl"] .site-footer__bottom .menu-item a {
  float: right;
  border-left: 1px solid #555;
  border-right: none;
}

.site-footer__bottom .menu-item:first-child a {
  padding-left: 0;
  /* LTR */
}

[dir="rtl"] .site-footer__bottom .menu-item:first-child a {
  padding-right: 0;
  padding-left: 12px;
}

.site-footer__bottom .menu-item:last-child a {
  padding-right: 0;
  /* LTR */
  border-right: none;
  /* LTR */
}

[dir="rtl"] .site-footer__bottom .menu-item:last-child a {
  padding-left: 0;
  padding-right: 12px;
  border-left: none;
}

/**
 * When the item is both first and last, :last-child overrides the right padding
 * when using [dir="rtl"]. We need this rule to remove the right padding.
 */
[dir="rtl"] .site-footer__bottom .menu-item:first-child:last-child a {
  padding-right: 0;
}

/**
 * @file
 * Styles for the skip link.
 */
.skip-link {
  left: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  z-index: 50;
  background: #444;
  background: rgba(0, 0, 0, 0.6);
  font-size: 0.94em;
  line-height: 1.7em;
  padding: 1px 10px 2px;
  border-radius: 0 0 10px 10px;
  border-bottom-width: 0;
  outline: 0;
}

.skip-link.visually-hidden.focusable:focus {
  position: absolute !important;
  color: #fff;
}

/**
 * @file
 * Styles for Droopler's tables.
 */
.table, table {
  border: 0;
  border-spacing: 0;
  font-size: 0.857em;
  margin-top: 10px;
  margin-bottom: 10px;
  width: 100%;
}

.table table, table table {
  font-size: 1em;
}

.table th, table th {
  padding: 0.75rem;
  border-style: none;
  border-top: 2px solid #fff;
  background: #1a2e7b;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
  vertical-align: middle;
  line-height: 1.2;
  color: #fff;
  font-size: 1rem;
}

.table thead tr th a, .table thead tr th a:hover, .table thead tr th a:focus, table thead tr th a, table thead tr th a:hover, table thead tr th a:focus {
  line-height: 1.2;
  color: #fff;
  font-size: 1rem;
}

.table thead tr th a, table thead tr th a {
  display: flex;
  justify-content: center;
  align-items: center;
}

.table thead tr th .tablesort, table thead tr th .tablesort {
  filter: brightness(0) invert(1);
  margin-left: 7px;
}

.table tbody tr, table tbody tr {
  padding: 0.1em 0.6em;
  background: #eeeeee;
  border-bottom: none;
}

.table tbody tr:nth-of-type(odd), table tbody tr:nth-of-type(odd) {
  background: #e4e4e4;
}

.table tbody tr td, table tbody tr td {
  padding: 0.75rem;
  vertical-align: top;
  border: 2px solid #fff;
  text-align: left;
  /* [dir="ltr"] */
}

.table tbody tr td:first-child, table tbody tr td:first-child {
  border-left: none;
}

.table tbody tr td:last-child, table tbody tr td:last-child {
  border-right: none;
}

[dir="rtl"] .table tbody td, [dir="rtl"] table tbody td {
  text-align: right;
}

/**
 * Responsive tables priorities.
 */
@media screen and (max-width: 37.5em) {
  /* 600px */
  th.priority-low,
  td.priority-low,
  th.priority-medium,
  td.priority-medium {
    display: none;
  }
}

@media screen and (max-width: 60em) {
  /* 920px */
  th.priority-low,
  td.priority-low {
    display: none;
  }
}

/**
 * @file
 * Visual styles for Bartik's ui-widget component.
 */
